.header {
  width: 100%;
  height: 0;
  padding-right: 24px;
  margin: 0;
  position: fixed;
  top: -90px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  justify-content: baseline;
  background-color: transparent;
  font-family: 'Cuprum', sans-serif;
  z-index: 999;
  color: white;
  -webkit-animation: showHeader 1s ease-in-out;
  animation: showHeader 1s ease-in-out;
  animation-fill-mode: forwards;
  animation-delay: 5.8s;
}

.header .navigationAuthor {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.header .header__logo {
  height: fit-content;
  margin-left: 8px;
  text-shadow: 1px 2px 2px rgba(0, 0, 0, 0.69);
  background-color: #0202020d;
  border-radius: 15px;
  z-index: 10;
  opacity: .90;
  letter-spacing: .2em;
  padding: 5px 8px;
  font-size: 12px;
}

.header .header__logo #author {
  text-transform: uppercase;
}

.header .header__logo .pageCategory {
  text-transform: capitalize;
}

.header .header__logo-aboutMe {
  background-color: #2a4d68;
  text-shadow: 1px 2px 2px rgba(0, 0, 0, 0.966);
  border: 1px solid white;
  border: none;
  z-index: 10;
  opacity: .85;
  letter-spacing: .2em;
  padding: 5px 10px;
  padding-top: 4.5px;
  font-size: 13px;
  text-transform: uppercase;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 100px;
  margin-top: 1px;
  color: white;
}

.header .header__logo-aboutMe .btn-text {
  color: white;
}

.header .contactLink:hover {
  transform: scale(1.3);
}

.header .header__navigation-tablet {
  display: none;
}

.header .header__navigation {
  width: 60px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header .header__navigation .menu,.header .header__navigation .menu-bg,.header .header__navigation .menu-burger {
  width: 60px;
  height: 60px;
  font-size: 30px;
  text-align: center;
}

.header .header__navigation .menu-bg {
  background: #663399;
  pointer-events: none;
  transition: .3s;
  right: 40px;
  top: 40px;
  transform: translate3d(50%, -50%, 0);
  transform-origin: center center;
}

.header .header__navigation .menu-bg.fs {
  transform: translate3d(50%, -50%, 0);
  width: 300vw;
  height: 300vw;
}

.header .header__navigation .menu-burger {
  color: white;
  -webkit-user-select: none;
  cursor: pointer;
  transition: .4s;
  transform-origin: center;
  position: relative;
  width: 55px !important;
  height: 32px !important;
  font-size: 18px;
  border-top: 2px solid white;
  border-bottom: 2px solid white;
}

.header .header__navigation .menu-burger.fs {
  transform: rotate(-180deg) translateY(10px);
}

.header .header__navigation .menu-items {
  position: absolute;
  font-weight: bold;
  font-size: 40px;
  letter-spacing: 10px;
  color: white;
  width: 100vw;
  text-align: center;
  opacity: 0;
  transition: .4s;
  margin-top: 100px;
  transform: translateY(-200%);
  pointer-events: none;
}

.header .header__navigation .menu-items div {
  transition: 1s;
  opacity: 0;
  margin-top: 0px;
}

.header .header__navigation .menu-items.fs {
  transform: translateY(0);
  pointer-events: auto;
  opacity: 1;
}

.header .header__navigation .menu-items.fs div {
  opacity: 1;
  margin-top: 75px;
}

.header .navigation__checkbox {
  display: none;
}

.header label {
  height: 40px;
  width: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.header .navigation__button {
  background-color: #020202;
  height: 60px;
  width: 60px;
  margin-right: 15px;
  position: fixed;
  z-index: 2000;
  text-align: center;
  cursor: pointer;
  color: white;
}

.header .navigation__background {
  background-color: #020202;
  height: 35px;
  width: 35px;
  opacity: 2;
  position: fixed;
  background-image: radial-gradient(#02020202, #0202020d);
  z-index: 1000;
  transition: transform 0.8s cubic-bezier(0.86, 0, 0.07, 1);
}

.header .navigation__nav {
  background-color: #0202020d;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1500;
  opacity: 0;
  width: 0;
  transition: all 0.8s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}

.header .navigation__list {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  list-style: none;
  text-align: center;
  width: 0;
}

.header .navigation__item {
  display: inline-block;
  margin: 1rem;
  text-align: left;
}

.header .navigation__item:hover {
  box-shadow: inset 230px 0 0 0 #08B;
  transition: all 0.6s;
}

.header .navigation__link:link,.header .navigation__link:visited {
  display: inline-block;
  font-size: 3rem;
  font-weight: 350;
  padding: 1rem 2rem;
  color: #fff;
  text-decoration: none;
  text-transform: uppercase;
}

.header .navigation__link:link span,.header .navigation__link:visited span {
  display: inline-block;
}

.header .navigation__link:hover,.header .navigation__link:active {
  color: #fff;
  font-weight: 700;
}

.header .navigation__checkbox:checked ~ .navigation__background {
  transform: scale(80);
}

.header .navigation__checkbox:checked ~ .navigation__nav {
  opacity: 1;
  width: 100vw;
}

.header .navigation__icon {
  position: relative;
}

.header .navigation__icon,.header .navigation__icon::before,.header .navigation__icon::after {
  width: 3rem;
  height: 2px;
  background-color: #fff;
  display: inline-block;
}

.header .navigation__icon::before,.header .navigation__icon::after {
  content: "";
  position: absolute;
  left: 0;
  transition: all .2s;
}

.header .navigation__icon::before {
  top: -.8rem;
}

.header .navigation__icon::after {
  top: .8rem;
}

.header .navigation__button:hover .navigation__icon::before {
  top: -1rem;
}

.header .navigation__button:hover .navigation__icon::after {
  top: 1rem;
}

.header .navigation__checkbox:checked + .navigation__button .navigation__icon {
  background-color: transparent;
}

.header .navigation__checkbox:checked + .navigation__button .navigation__icon::before {
  top: 0;
  transform: rotate(135deg);
}

.header .navigation__checkbox:checked + .navigation__button .navigation__icon::after {
  top: 0;
  transform: rotate(-135deg);
}

.header .navigation__button:active {
  background-color: transparent;
}

.header .navigation__button:active > .navigation__list {
  width: 100vw;
  height: 100vh;
}

.showItems {
  width: 100%;
}

.hero {
  height: 100vh;
  width: 100vw;
  padding: 0 25px;
