  align-items: center;
  justify-content: center;
  align-items: center;
  align-content: center;
  overflow: hidden;
  -webkit-animation: notShow 1s ease-in-out;
  animation: notShow 1s ease-in-out;
  animation-fill-mode: forwards;
  animation-delay: 5.4s;
}

.hero .heroBackground {
  width: 60vw;
  position: absolute;
  -webkit-animation-name: spin;
