.intro {
  bottom: -50px;
  position: absolute;
}

svg.intro {
  width: 95%;
  max-width: 800px;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
  background-color: #0c0c0c;
}

svg.intro .text {
  display: none;
}

svg.intro.go {
  background-color: transparent;
}

svg.intro.go .text {
  display: block;
  font-family: 'Cuprum', sans-serif;
  font-size: 20px;
  text-transform: uppercase;
}

svg.intro.go .text-stroke {
  stroke-width: 2.5px;
  stroke-dashoffset: -900;
  stroke-dasharray: 900;
  fill: none;
  stroke: white;
  stroke-linecap: butt;
  stroke-linejoin: round;
  animation: dash 1.8s ease-in-out forwards;
}

svg.intro.go .text-stroke:nth-child(2) {
  animation-delay: .2s;
}

svg.intro.go .text-stroke:nth-child(3) {
  animation-delay: .8s;
}

svg.intro.go .text-stroke-2 {
  stroke: #f6bdfa;
  animation-delay: 5.1s;
}

svg.intro.go .text-stroke-2 {
  stroke: white;
  animation-delay: 2.1s;
}

svg.intro.go .text-stroke:nth-child(5) {
  animation-delay: 2.1s;
}

svg.intro.go .text-stroke:nth-child(6) {
  animation-delay: 2.7s;
}

