@keyframes showHeader {
  0% {
    height: 0;
    top: -90px;
  }

  100% {
    height: 60px;
    display: flex;
    top: 0;
  }
}

@keyframes expande {
  0% {
    width: 60px;
    height: 60px;
  }

  100% {
    width: 200%;
    height: 200%;
  }
}

@keyframes notShow {
  0% {
    width: 100vw;
    height: 100vh;
    top: 0;
    bottom: 0;
    opacity: 1;
  }

  100% {
    left: 0;
    top: -60vh;
    width: 100vw;
    height: 0vh;
  }
}

@keyframes spin {
  0% {
    opacity: 0;
    transform: rotate(0deg);
  }

  3% {
    opacity: 1;
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes dash {
  100% {
    stroke-dashoffset: 0;
  }
}

@keyframes showJob {
  0% {
    opacity: 0;
    height: 0;
  }

  100% {
    opacity: 1;
    height: 23px;
  }
}

@keyframes moveInLeft {
  0% {
    opacity: 0;
    transform: translateX(-10rem);
  }

  80% {
    transform: translateX(1rem);
  }

  100% {
    opacity: 1;
    transform: translate(0);
  }
}
